<template>
    <div class="takecash">
        <van-nav-bar
                title="钱包提现"
                left-arrow
                right-text="提现记录"
                @click-left="onClickLeft"
                @click-right="onClickRight"
                fixed
        ></van-nav-bar>
        <div style="margin-top:15vw;">
            <div class="form-box">
                <van-field
                        v-model="remain"
                        label="可用余额"
                        readonly
                        input-align="right"
                ></van-field>
                <van-field
                        v-model="takenum"
                        label="提现数量"
                        placeholder="请输入提现数量"
                ></van-field>
                <van-field
                        v-model="walet_addr"
                        label="钱包地址"
                        placeholder="必填"
                        required
                        right-icon="ellipsis"
                        @click-right-icon="onClickRightIcon"
                ></van-field>
                <van-field
                        v-model="realnum"
                        label="到账数量"
                        readonly
                        disabled
                        input-align="right"
                ></van-field>
            </div>
            <div class="form-box" style="margin-top: 3vw">
                <van-field
                        v-model="phonenum"
                        label="手机号"
                        readonly
                        disabled
                        input-align="right"
                ></van-field>
                <van-field
                        v-model="sms"
                        center
                        clearable
                        label="验证码"
                        placeholder="请输入验证码"
                >
                    <a slot="button" @click="getCode">{{sms_button_text}}</a>
                </van-field>
                <van-field
                        v-model="walet_password"
                        label="交易密码"
                        placeholder="必填"
                        type="password"
                        required
                ></van-field>
            </div>
            <p class="remark">提现备注：提现矿工费{{takecash_percent}}</p>
            <van-button type="info" style="width: 90vw;font-weight: bold;margin-top: 5vw;">确定</van-button>
        </div>
        <van-action-sheet
                v-model="show_walet_addr_options"
                title="选择钱包"
        >
            <van-radio-group v-model="radio">
                <van-cell-group>
                    <van-cell v-for="item in walet_addr_list" :title="item.remark" :label="item.walet_addr" clickable @click="SelectionRadio(item)">
                        <van-radio slot="right-icon" :name="item" />
                    </van-cell>
                </van-cell-group>
            </van-radio-group>
        </van-action-sheet>
        <van-popup
                v-model="show_takecash_record"
                position="right"
                get-container="#app"
                :style="{width:'100vw', height:'100vh', background:'white'}"
                :duration="0.3"
                :overlay="false"
        >
            <takecash-record></takecash-record>
        </van-popup>
    </div>
</template>

<script>
    import { NavBar, Popup, Field, ActionSheet, RadioGroup, Radio, CellGroup, Cell, Button } from 'vant';
    import takecashRecord from '@/components/common/record/takecashRecord.vue';
    export default {
        name: "takecash",
        components: {
            [NavBar.name]: NavBar,
            [Popup.name]: Popup,
            [Field.name]: Field,
            [ActionSheet.name]: ActionSheet,
            [RadioGroup.name]: RadioGroup,
            [Radio.name]: Radio,
            [CellGroup.name]: CellGroup,
            [Cell.name]: Cell,
            [Button.name]: Button,
            takecashRecord
        },
        data() {
          return {
              show_takecash_record: false, // 是否跳转提现记录
              show_walet_addr_options: false, //是否展示钱包地址列表
              remain: '0 SHT', // 剩余数量
              takenum: '', //提现数量
              walet_addr: '', // 钱包地址
              realnum: '0 SHT', // 实际到账数量
              phonenum: '1232***2342', // 手机号
              sms: '', // 验证码
              sms_button_text: '获取验证码', // 验证码按钮
              init_sms: 59, // 验证码倒计时
              get_code_status: false, // 获取验证码状态
              code_count_down_handle: null, // 验证码倒计时句柄
              walet_password: '', // 交易密码
              takecash_percent: '1%', // 矿工百分比
              walet_addr_list: [ // 钱包列表
                  {
                      id: 0,
                      remark: '钱包1',
                      walet_addr: 'skdflskdlfsdlfsldk'
                  },
                  {
                      id: 1,
                      remark: '钱包2',
                      walet_addr: 'sdlkfsldkfsldksdlf'
                  }
              ],
              radio: '',
          }
        },
        methods: {
            onClickLeft () {
                // this.$parent.$parent.poup_show = false;
                this.$router.go(-1);
            },
            onClickRight () {
                // this.show_takecash_record = true
                this.$router.push({path:'takecashRecord'})
            },
            onClickRightIcon () {
                this.show_walet_addr_options = true;
            },
            SelectionRadio (item) {
                this.walet_addr = item.walet_addr;
                this.show_walet_addr_options = false;
            },
            getCode () {
                if(!this.get_code_status){
                    this.get_code_status = true;
                    let _this = this;
                    this.sms_button_text = '60s';
                    this.code_count_down_handle = setInterval(function () {
                        console.log(_this.sms_button_text)
                        if(_this.init_sms == 0){
                            _this.sms_button_text = '获取验证码';
                            _this.init_sms = 59;
                            clearInterval(_this.code_count_down_handle);
                            this.get_code_status = false;
                            return false;
                        }
                        _this.sms_button_text = _this.init_sms+'s';
                        _this.init_sms--;
                    }, 1000);
                }
            }
        }
    }
</script>

<style scoped>
.takecash{
    width: 100vw;
    height: 100vh;
    background: #f5f5f5;
    position: fixed;

}
.form-box{
    width: 98vw;
    background: white;
    margin: auto;
}
.remark{
    text-align: right;
    color: red;
    line-height: 12vw;
    box-sizing: border-box;
    padding: 0px 3vw;
    font-size: 3.5vw;
}
</style>